























import Vue from 'vue';
import { mapGetters } from 'vuex';
import Card from '@/components/UI/Users/Cards/Card.vue';
import Error from '@/components/UI/Users/Cards/Partials/Error.vue';
import { pagination } from '@/types/store/pagination';

export default Vue.extend({
  /* eslint-disable vue/return-in-computed-property */
  /* eslint-disable vue/no-side-effects-in-computed-properties */
  components: { Card, Error },
  props: ['type'],
  data() {
    return {
      userName: '',
      dataLoaded: false,
    };
  },
  methods: {
    getComponent() {
      switch (this.type) {
        case 'card':
          return Card;
        default:
          return Card;
      }
    },
  },
  computed: {
    /* eslint-disable no-return-assign */
    ...mapGetters([
      'GET_USERS',
      'GET_ERRORS',
      'GET_PAGINATION',
      'GET_DATA_STATUS',
    ]),
    users: {
      get() {
        const { from, to } = this.GET_PAGINATION;
        return this.GET_USERS.slice(from, to);
      },
      set(val: pagination) {
        const { from, to } = val;
        return this.GET_USERS.slice(from, to);
      },
    },
    error() {
      return this.GET_ERRORS;
    },
  },
  watch: {
    GET_PAGINATION: {
      immediate: true,
      handler(val: pagination) {
        this.users = val;
      },
    },
  },
});
